<template>
  <div ref="scrollwrapper" class="article-list">
    <div
      class="wrapper"
      v-infinite-scroll="loadmore"
      infinite-scroll-disabled="loading"
      infinite-scroll-distance="10"
    >
      <ul v-for="(item,index) in stories" :key="index">
        <div v-if="item.date" :class="{timespan: item.date}">{{date2str(item.date)}}</div>
        <li v-for="(news,index) in item.stories" :key="index" @click="openArticle(news.id)">
          <!-- <div class="wrapper" v-if="item.images[0]"> -->
          <div>
            <span>{{news.title}}</span>
            <img v-lazy="replaceUrl(news.images[0])" :alt="news.title">
          </div>
          <!-- </div> -->
        </li>
      </ul>
      <!--  -->
      <div class="loading" v-show="loading">
        <i class="el-icon-loading"/>
      </div>
    </div>
  </div>
</template>

<script>
import api from "../assets/js/api";

export default {
  data() {
    return {
      loading: false,
      date: Number,
      stories: [], //
      page: 0
    };
  },
  created() {
    this.$store.commit("CLEAR_ID_LIST", []);
    this.getTodayData();
    //store ??
  },
  // mounted() {
  //   //scroll
  //   window.onscroll = this.loadmore;
  // },
  // updated() {
  //   //this.stories
  //   this.$store.commit("getidList", this.getidList);
  // },
  // updated() {
  //   this.$nextTick(() => {
  //     this.resetToday();
  //   });
  // },
  computed: {
    today() {
      let today = new Date();
      let y = today.getFullYear().toString();
      let m = (today.getMonth() + 1).toString();
      m = m.length === 1 ? "0" + m : m;
      let d = today.getDate().toString();
      d = d.length === 1 ? "0" + d : d;
      today = y + m + d;
      return today;
    }
  },
  // watch: {
  //   data: {
  //     handler(e) {
  //       //this.stories, , stories
  //       let obj = {};
  //       obj.date = this.data[this.page].date;
  //       this.stories.push(obj);
  //       this.stories = this.stories.concat(this.data[this.page].stories);
  //       // this.$nextTick(() => {
  //       this.$store.commit("getidList", this.getidList);
  //       this.page++;
  //       // });
  //     },
  //     deep: true
  //   }
  // },

  // stories: {
  //   handler() {
  //     this.$store.commit("getidList", this.getidList);
  //   },
  //   deep: true
  // },

  methods: {
    getTodayData() {
      this.$axios(api.newsLatest).then(res => {
        // console.log(res);
        let obj = {};
        obj.date = res.data.date;
        obj.stories = res.data.stories;
        this.stories.push(obj);
        this.getidList(res.data.stories);
        this.date = res.data.date;
      });
    },
    resetToday() {
      console.log(this.stories[0]);
      if (this.stories[0].date == this.today) {
        this.date = this.today;
      } else {
        this.date = this.stories[0].date;
      }
    },
    replaceUrl(oldurl) {
      return oldurl.replace(
        /http\w{0,1}:\/\/p/g,
        "https://images.weserv.nl/?url=p"
      );
    },
    openArticle(id) {
      //idid
      this.$store.commit("getId", id);
      //
      this.$router.push({
        name: "article",
        params: {
          id
        }
      });
      // console.log(id);
    },
    getidList(arr) {
      let list = [];
      arr.forEach(el => {
        if (el.id) {
          list.push(el.id);
        }
      });
      this.$store.commit("GET_ID_LIST", list);
    },
    date2str(d) {
      let y = d.substr(0, 4),
        m = d.substr(4, 2),
        date = d.substr(6, 2),
        day = new Date(y, m - 1, date).getDay();
      switch (day) {
        case 0:
          day = "";
          break;
        case 1:
          day = "";
          break;
        case 2:
          day = "";
          break;
        case 3:
          day = "";
          break;
        case 4:
          day = "";
          break;
        case 5:
          day = "";
          break;
        case 6:
          day = "";
          break;
      }
      return m + "" + date + "" + " " + day;
    },
    //  vueScroller+scroll
    // //scroll
    // scroller() {
    //   let scrollHeight =
    //       document.body.scrollTop || document.documentElement.scrollTop,
    //     windowHeight = window.innerHeight,
    //     bodyHeight = document.body.offsetHeight;
    //   // console.log(scrollHeight + ":" + windowHeight + ":" + bodyHeight);
    //   //+>=
    //   return scrollHeight + windowHeight >= bodyHeight;
    // },

    //
    loadmore() {
      // if (this.scroller()) {
      //   this.judgeDate(this.date);
      // console.log("more");
      if (!this.loading) {
        this.loading = true;
        setTimeout(() => {
          this.$axios(api.newsBefore + this.date).then(res => {
            // console.log(res);
            if (!res.data.status) {
              this.stories.push(res.data);
              this.date--;
              this.getidList(res.data.stories);
            }
            //     let obj = res.data;
            //     this.data.push(obj);
            //     this.$nextTick(() => {
            //       this.judgeDate(this.date);
            this.loading = false;
            //     });
            //     // //
            //     // this.$nextTick(() => {
            //     //   let dataObj = {};
            //     //   dataObj.date = this.data[this.page].date;
            //     //   this.stories.push(dataObj);
            //     //   this.stories = this.stories.concat(this.data[this.page].stories);
            //     //   this.$store.commit("getidList", this.getidList);
            //     // });
          });
        }, 2000);
      }
      // }
    },
    judgeDate(date) {
      //
      let strDate = date.toString();
      let y = strDate.substr(0, 4);
      let m = strDate.substr(4, 2);
      let d = strDate.substr(6, 2);
      //00 -> -1
      if (d === "00") {
        d = new Date(y, m - 1, 0).getDate().toString();
        m = (m - 1).toString();
      }
      //
      d = d.length === 1 ? "0" + d : d;
      m = m.length === 1 ? "0" + m : m;
      this.date = y + m + d;
    }
  }
};
</script>


